






















































































































































































































































import { Component, Vue } from "vue-property-decorator";
import UserAdd from "./add.vue";

// import IhDialog from "@/components/IhDialog.vue";
import MyOrganization from "@/components/MyOrganization.vue";
import MyPagination from "@/components/my-pagination.vue";
import { organization, userList } from "../../api/system";
// import { getResourceByIdUsingGET } from "../../api/system";
@Component({
  components: { UserAdd, MyPagination, MyOrganization }
})
export default class UserList extends Vue {
  options: any = [
    {
      value: "1",
      label: "下拉数据1"
    },
    {
      value: "2",
      label: "下拉数据2"
    }
  ];
  addData: any = null;
  value: any = "";
  searchOpen = true;
  myOrganizationVisible = false;

  currentPage: any = 1;
  valuedate: any = new Date().getTime();
  // valuedate: any ='2020-07-01';
  tableData: any = [];
  total: any = null;
  formatter(row: any) {
    return row.name;
  }

  openToggle() {
    this.searchOpen = !this.searchOpen;
  }
  dialogVisible = false;

  add(data: any) {
    this.addData = data;
    this.dialogVisible = true;
  }

  finish(data: any) {
    console.log(data);
    this.valueVal = data?.id;
    this.valueZZ = data?.name;
  }

  valueId: any = "1D29BB468F504774ACE653B946A393EE"; // 初始ID（可选）
  // valueId: any = null; // 初始ID（可选）
  props = {
    // 配置项（必选）
    value: "id",
    label: "name",
    children: "subs",
    defaultExpandedKeys: [
      "10B1F16BDC5E7F33E0532429030A8871",
      "105DB2C289397D50E0532429030A3DE0"
    ]
    // defaultCheckedKeys: ["1D29BB468F504774ACE653B946A393EE"]
  };
  // 选项列表（必选）
  list: any = [];
  async created() {
    let organizationDTree = await organization();
    this.list = organizationDTree;

    this.getUserList();
  }
  async getUserList() {
    const { list, total } = await userList();
    console.log(list, total);
    this.tableData = list;
    this.total = total;
  }

  getValue(value: any) {
    this.valueId = value;
    console.log(this.valueId);
  }

  search() {
    console.log(this.valuedate);
    this.currentPage = 1;
    this.getUserList();
  }
  handleSizeChange(a: any) {
    console.log(a);
    this.getUserList();
  }
  handleCurrentChange(a: any) {
    console.log(a);
    this.getUserList();
  }
  set() {
    this.valueId = "2c92808270b81f9d0170bee437800015";
  }
  valueZZ: any = null;
  valueVal: any = null;
  selectZZ() {
    console.log("选择归属组织");
    this.myOrganizationVisible = true;
  }
  clear() {
    console.log("clear");
    this.valueVal = null;
    this.valueZZ = null;
  }
  info(scope: any) {
    this.$router.push({
      path: "/user/info",
      query: { id: scope.row.id }
    });
  }
  edit(scope: any) {
    this.add(scope.row);
  }
  async remove(scope: any) {
    try {
      await this.$confirm("是否确定删除?", "提示");
      this.tableData.splice(scope.$index, 1);
      this.$message({
        type: "success",
        message: "删除成功!"
      });
    } catch (error) {
      console.log(error);
    }
  }
  async locking(scope: any) {
    console.log(scope);
    try {
      await this.$confirm("是否确定锁定用户?", "提示");

      this.$message({
        type: "success",
        message: "锁定成功!"
      });
      this.search();
    } catch (error) {
      console.log(error);
    }
  }
  async activation(scope: any) {
    console.log(scope);
    try {
      await this.$confirm("是否确定激活用户?", "提示");

      this.$message({
        type: "success",
        message: "激活成功!"
      });
      this.search();
    } catch (error) {
      console.log(error);
    }
  }
  async resetPassword(scope: any) {
    console.log(scope);
    try {
      await this.$confirm("是否确定重置密码?", "提示");

      this.$message({
        type: "success",
        message: "密码重置成功!"
      });
    } catch (error) {
      console.log(error);
    }
  }
}
